import React from 'react'
import MenuIcon from '@mui/icons-material/Menu';
import TranslateIcon from '@mui/icons-material/Translate';
import LightModeIcon from '@mui/icons-material/LightMode';
import WidgetsIcon from '@mui/icons-material/Widgets';
import NotificationAddIcon from '@mui/icons-material/NotificationAdd';
import { Avatar } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';


const MenuBar = () => {
  return (
    <div className=' border p-2 flex justify-between m-4 mx-8 rounded-md '>
    <div className='flex items-center text-gray-500 hover:text-black '>
      <MenuIcon/>
     <div className='ml-2'> <SearchIcon/> <input className='outline-none px-2'  placeholder='Search' type="text" /> </div>  
    </div> 

    <div className='flex justify-evenly items-center mr-4'>
        <TranslateIcon className='text-gray-500 hover:text-black ml-3'/>
        <LightModeIcon className='text-gray-500 hover:text-black ml-3' />
        <WidgetsIcon className='text-gray-500 hover:text-black ml-3' />
        <NotificationAddIcon className='text-gray-500 hover:text-black ml-3' />
        <Avatar className='text-gray-500 hover:text-black ml-3'/>

    </div>

  </div>
  )
}

export default MenuBar