import React from 'react';
import { Bar } from 'react-chartjs-2';
import {Chart as ChartJS} from 'chart.js/auto'

const BarChart = ({ chartData,id }) => {
 

  return (
    <div  className={ id==="mainBar" ? " w-[100%] h-[250px] pr-2 flex justify-center " : "h-[300px] pr-2 flex justify-center w-[100%]"}>
      <Bar data={chartData}  />   
    </div>
  );
}

export default BarChart;
