import React, { useEffect, useState } from 'react'
import BarChart from './BarChart';
import MenuBar from './MenuBar';
import MyChart from './MyChart';
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth';
import TopicIcon from '@mui/icons-material/Topic';
import AutoAwesomeMosaicIcon from '@mui/icons-material/AutoAwesomeMosaic';
import PublicIcon from '@mui/icons-material/Public';
import LanguageIcon from '@mui/icons-material/Language';
import LocationCityIcon from '@mui/icons-material/LocationCity';
import SourceIcon from '@mui/icons-material/Source';
import FactoryIcon from '@mui/icons-material/Factory';
import axios from 'axios';


const Body = () => {

  const [dbData,setDbData]=useState();

  useEffect(()=>{
    
    axios.get('http://localhost:8080/api/')
      .then(response =>{
        console.log(response.data)
        setDbData(response.data.slice(0,10)) } )
      .catch(error => console.error("error hai"));

    
},[])

const [switchData, setSwitchData]= useState();

function handleClick(option){
        switch(option){
          case "end_year":
              setSwitchData("end_year")
              break;
          case "topics":
              setSwitchData("topic")
              break; 
          case "sector":
              setSwitchData("sector")
              break;
          case "region":
              setSwitchData("region")
              break;
          case "country":
              setSwitchData("country")
              break;
            
          case "source":
              setSwitchData("source")
              break;
            case "pestle":
              setSwitchData("pestle")
              break;      
                          
            
         }
}
 
  return (
    <div>
      <MenuBar/>

     <div className='mx-8 '>
      <div className='w-full h-[200px]  rounded-md flex '>
        {
        dbData ? <>
        <div className=' border-2 w-1/3 rounded-md h-full ml-2'> <MyChart switchData={switchData}  dbData={ dbData} chart='bar' /> </div>
        <div className=' rounded-md border-2 h-full w-1/3  ml-2'> <MyChart switchData={switchData}  dbData={ dbData} chart='line' /></div>
        <div className=' rounded-md border-2 h-full w-1/3  ml-2'> <MyChart switchData={switchData}  dbData={dbData} chart='pie' /> </div>
        </>
      :
      ""
        }
      </div>
     

    <div className=' border-2 rounded-md w-full h-[500px] mt-4 ml-2'>
      <div className='p-8 '>
      <div className=''> 
        <h1> Reports</h1>
         <p>Yearly Reports Overview</p>
      </div>

      <div className=' w-full flex h-[100px] mt-6 ml-2' >
      <div className=' border-2 w-1/6 rounded-md hover:bg-purple-400 h-full ml-3 flex flex-col items-center justify-center' onClick={()=>handleClick("end_year")}>
       <div className="p-3 mb-1 rounded-md bg-slate-100"><CalendarMonthIcon/> </div>
       <h1>End Year</h1>
      </div>
      <div className=' border-2 w-1/6 rounded-md hover:bg-purple-400 h-full ml-3 flex flex-col items-center justify-center' onClick={()=>handleClick("topics")}>
       <div className="p-3 mb-1 rounded-md bg-slate-100"><TopicIcon/> </div>
       <h1>Topics</h1>
      </div>
      <div className=' border-2  w-1/6 rounded-md hover:bg-purple-400 h-full ml-3 flex flex-col items-center justify-center' onClick={()=>handleClick("sector")}>
       <div className="p-3 mb-1 rounded-md bg-slate-100"><AutoAwesomeMosaicIcon/> </div>
       <h1>Sector</h1>
      </div>
      <div className=' border-2 w-1/6 rounded-md hover:bg-purple-400 h-full ml-3 flex flex-col items-center justify-center' onClick={()=>handleClick("region")}>
       <div className="p-3 mb-1 rounded-md bg-slate-100">< LanguageIcon /> </div>
       <h1>Region</h1>
      </div>
      <div className=' border-2 w-1/6 rounded-md hover:bg-purple-400 h-full ml-3 flex flex-col items-center justify-center' onClick={()=>handleClick("country")}>
      <div className="p-3 mb-1 rounded-md bg-slate-100"><PublicIcon /> </div>
       <h1>Country</h1>
      </div>
   
        <div className=' border-2 w-1/6 rounded-md hover:bg-purple-400 h-full ml-3 flex flex-col items-center justify-center' onClick={()=>handleClick("source")}>
        <div className="p-3 mb-1 rounded-md bg-slate-100"><SourceIcon /> </div>
       <h1>Source</h1>
        </div>
      <div className=' border-2 w-1/6 rounded-md hover:bg-purple-400 h-full ml-3 flex flex-col items-center justify-center' onClick={()=>handleClick("pestle")}>
        <div className="p-3 mb-1 rounded-md  bg-slate-100"><FactoryIcon /> </div>
       <h1>Pest</h1>
        </div>
      </div>
      
      <div className='w-full' >
      <div className=' mt-5 border-2 w-full rounded-md h-full ml-2'> { dbData ?  <MyChart switchData={switchData} dbData={dbData} id="mainBar" chart='bar' /> : "" }</div>
       
      </div>
        
      </div>
      </div>
    </div>
    </div>
    
  )
}

export default Body
