import React, { useEffect, useState } from 'react';
import BarChart from './BarChart';
import LineChart from './LineChart';
import PieChart from './PieChart';

const MyChart = ({ dbData, chart, id, switchData }) => {
  console.log(dbData);
  console.log(switchData);

  const [Data, setData] = useState({
    labels: dbData && dbData.map(data => data.end_year),
    datasets: [
      {
        label: "Intensity",
        data: dbData && dbData.map(data => data.intensity),
      },
      {
        label: "Likelihood",
        data: dbData && dbData.map(data => data.likelihood),
      },
      {
        label: "Relevance",
        data: dbData && dbData.map(data => data.relevance),
      },
    ],
  });

  useEffect(() => {
    if (dbData && switchData) {
      setData(prevData => ({
        ...prevData,
        labels: dbData.map(data => data[switchData])
      }));
    }
  }, [dbData, switchData]);

  return (
    <div className='w-[100%]'>
      <div className="w-[100%] h-full">
        <div className='flex w-[100%] h-full justify-between'>
          {chart === 'bar' && <BarChart chartData={Data} id={id} />}
          {chart === 'line' && <LineChart chartData={Data} />}
          {chart === 'pie' && <PieChart chartData={Data} />}
        </div>
      </div>
    </div>
  );
};

export default MyChart;
